<template>
  <div id="app">
    <transition name="router-fade" mode="out-in">
      <keep-alive>
        <router-view />
      </keep-alive>
    </transition>
    <van-tabbar
    v-model="active"
    active-color="#07c160"
    inactive-color="#000"
    @click.native='clickItem(active)'
    >
      <van-tabbar-item icon="chat-o" name='home'>微信</van-tabbar-item>
      <van-tabbar-item icon="friends-o" name='addressBook'>通讯录</van-tabbar-item>
      <van-tabbar-item icon="comment-circle-o" name='find'>发现</van-tabbar-item>
      <van-tabbar-item icon="contact" name='my'>我</van-tabbar-item>
    </van-tabbar>
</div>
</template>

<script>

  export default {

    name: 'App',
    data(){
      return{
         active:'home',
         scroll
      }
    },
    methods:{
      clickItem(name) {
        this.$router.push('/'+name);
      },
    },

  }
</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /*text-align: center;*/
    color: #2c3e50;
   
  }
  /*p{
    font-size: .6rem;
    background: green;
  }*/
  .router-fade-enter-active, .router-fade-leave-active {
      transition: opacity .3s;
  }
  .router-fade-enter, .router-fade-leave-active {
      opacity: 0;
  }
  #app >>> .van-tabbar-item__text {
    font-size: .28rem !important;
  }
</style>



