<template>
	<div id="find">
		<p>发现</p>
		<h2>1</h2>
		<h2>2</h2>
		<h2>3</h2>
		<h2>4</h2>
		<h2>5</h2>
	</div>
</template>
<script>

	export default{
		name:'find',
		data(){
			return{
				findTop:0,
			}
			
		},
		activated() {
	      document.getElementById('find').scrollTop = this.findTop || 0
	    },
	    beforeRouteLeave (to, from, next) {
		    let find = document.getElementById('find')
		    this.findTop = find.scrollTop || 0
		    next()
		},

	}
</script>
<style>
	
</style>


